import React from 'react';
import { HashRouter as Router, Routes, Route, useNavigate } from 'react-router-dom';
import { Container } from 'react-bootstrap';

// Import components
import Navigation from './components/Navigation';
import Dashboard from './pages/Dashboard';
import CreateStrategy from './pages/CreateStrategy';
import Backtest from './pages/Backtest';
import StrategyDetails from './pages/StrategyDetails';
import BacktestResults from './pages/BacktestResults';

// This component gives us access to the navigate function
// and exposes it to the window object for our redirect script
function RouterListener() {
  const navigate = useNavigate();
  
  React.useEffect(() => {
    // Expose navigate to window for the redirect script
    window.routerInstance = {
      navigate: (path) => navigate(path)
    };
  }, [navigate]);
  
  return null;
}

function App() {
  return (
    <Router>
      <RouterListener />
      <div className="App">
        <Navigation />
        <Container className="py-4">
          <Routes>
            <Route path="/" element={<Dashboard />} />
            <Route path="/create-strategy" element={<CreateStrategy />} />
            <Route path="/strategy/:id" element={<StrategyDetails />} />
            <Route path="/backtest" element={<Backtest />} />
            <Route path="/backtest-results/:id" element={<BacktestResults />} />
          </Routes>
        </Container>
      </div>
    </Router>
  );
}

export default App;
